.calendar {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.block {
  box-sizing: border-box;
  width: 350px;
  height: 400px;

  padding: 20px;
  background: #283041;
}

.inner {
  height: 150px;
}

.hidden {
  width: 350px;
  height: 400px;

  visibility: hidden;
}
